<template>
    <div>
        <h1>users profile</h1>

        <div class="col-md-8 order-md-1">
            <form class="needs-validation" @submit.prevent="updateUser()">
                <div class="row">
                    <div class="col-md-4 mb-3">
                        <label for="firstName">First Name(Persian Letter only)</label>   
                        <input  id="firstName" type="text" class="form-control" placeholder="First Name..." v-model="user.firstName">
                    </div>
                    <div class="col-md-4 mb-3">
                        <label for="lastName">Last Name(Persian Letter only)</label>
                        <input id="lastName" type="text" class="form-control" placeholder="Last Name..." v-model="user.lastName">
                    </div>
                    <div class="col-md-4 mb-3">
                        <label for="age">Age(Number only)</label>   
                        <input id="age" type="number" class="form-control" placeholder="Age..." v-model="user.age">
                    </div>
                </div>

                <div class="mb-3">
                    <label for="email">Email</label>
                    <input type="email" class="form-control" id="email" placeholder="you@example.com" v-model="user.email">
                    <div class="invalid-feedback">
                        Please enter a valid email.
                    </div>
                </div>


                <div class="mb-3">
                    <label for="phoneNumber">Phone Number</label>
                    <input type="text" class="form-control" id="phoneNumber" placeholder="Phone Number..." v-model="user.phoneNumber">
                    <div class="invalid-feedback">
                        Please enter a valid password.
                    </div>
                </div>


                <div class="mb-3">
                    <label for="studentNumber">Student Number(if amirkabiri otherwise ignore)</label>
                    <input type="text" class="form-control" id="studentNumber" placeholder="Amirkabir Student Number..." v-model="user.studentNumber">
                    <div class="invalid-feedback">
                        Please enter a valid studentNumber.
                    </div>
                </div>

                <hr class="mb-4">
                <button class="btn btn-primary btn-lg btn-block"
                        type="submit">Update User</button>
            </form>
        </div>

    </div>
</template>

<script>
    import axios from 'axios';
    import jalali from 'jalali-moment';
    export default {
        name: "UsersProfile",
        data() {
            return {
                user : {},
                btnText:"Update User"
            }
        },
        methods : {
            getJalali(date) {
                return jalali(date);
            },
            getUserWithId(){
                console.log("Getting user with id " + this.$route.params.id);
                axios({
                url : this.$store.getters.usersApi + this.$route.params.id,
                method : 'GET',
                headers : {
                    'Authorization' : 'Bearer ' + this.$store.getters.token,
                    "Content-Type" : "application/json"
                }
            }).then(response => {
                console.log(response);
            }).catch(error => {
                console.log(error);
                console.log("user obj to send " , this.user);
                if(error.response)
                    console.log(error.response);
            }).finally(() => {

            })
            },
            getUser() {
                axios({
                    url : this.$store.getters.usersApi,
                    method : 'GET',
                    headers : {
                        "Authorization" : "Bearer " + this.$store.getters.token,
                        "Content-Type" : "application/json"
                    }
                }).then(response => {
                    console.log(response);
                    for(let i = 0 ; i < response.data.length ;i++){
                        if(response.data[i]._id === this.$route.params.id){
                            this.user = response.data[i];
                            break;
                        }
                    }
                }).catch(error => {
                    console.log(error);
                    if(error.response)
                        console.log(error.response)
                }).finally(() =>{

                })
            },
            updateUser() {
                console.log("update user with id ", this.$route.params.id);
                this.btnText = "loading ..."
                let updatedUser = {
                    age : this.user.age,
                    email : this.user.email,
                    firstName : this.user.firstName,
                    lastName : this.user.lastName,
                    phoneNumber : this.user.phoneNumber,
                    studentNumber : this.user.studentNumber
                }
                axios({
                    url : this.$store.getters.usersApi + this.$route.params.id,
                    method : "PATCH",
                    data: updatedUser,
                    headers : {
                        "Authorization" : "Bearer " + this.$store.getters.token,
                        "Content-Type" : "application/json"
                    }
                }).then(response => {
                    console.log("done")
                    console.log(response);
                    this.btnText = "Update User"
                  this.$notify({
                    group : "main",
                    text : "User updated successfully",
                    title : "Success",
                    type : "success",
                    position: "top center",
                    duration: 3000,
                  })
                }).catch(error => {
                    console.log(error);
                  this.$notify({
                    group : "main",
                    text : "Error updating user.<br>"+error.response.data.message,
                    title : "Error.",
                    type : "error",
                    position: "top center",
                    duration: 5000,
                  })
                })
            }
        },
        created() {
            // this.getUserWithId();
            this.getUser();
        }
    }
</script>

